@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Aladin&display=swap);
.menu {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.menu-btn {
  background: #fffbf1;
  border-radius: 87px;
  height: 4vw;
  width: 14vw;
  color: #e59379;
  border: none;
  font-size: 2.3vw;
  font-family: 'Aladin', cursive;
  transition: all 0.1s ease;
  cursor: pointer;
}

.menu-btn-selected {
  border-radius: 87px;
  height: 4vw;
  width: 14vw;
  border: none;
  font-size: 2.3vw;
  font-family: 'Aladin', cursive;
  transition: all 0.1s ease;
  cursor: pointer;
  background: #e7e2d8;
  color: #bd5f42;
}

.main-timer-block {
  height: 80%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.time {
  margin-top: 4vw;
  color: #fffbf1;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 10vw;
  margin-bottom: 2vw;
}

.fas {
  color: #fffbf1;
  font-size: 4vw;
  cursor: pointer;
  transition: all 0.3s ease;
}

.fas:hover {
  color: #e6e3db;
}

.timer-btns {
  display: flex;
  justify-content: space-evenly;
  width: 30%;
}

.timer {
  height: 60%;
  width: 100%;
}

h3 {
  margin: 0;
}

.timer-settings-block-wrapper {
  width: 100%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.timer-settings-block {
  display: flex;
  align-items: center;
  height: 70%;
}

.timer-option-length {
  height: 3vw;
  width: 5vw;
  background: #fffbf1;
  border-radius: 87px;
  outline: none;
  font-size: 2.1vw;
  text-align: center;
  color: #e59379;
  border: none;
  transition: all 0.3s ease;
  font-family: 'Aladin', cursive;
}

.timer-option-length:focus {
  background: #f1ede1;
  color: #fb6b3e;
}

.timer-settings-name {
  color: #fffbf1;
  font-style: normal;
  font-weight: normal;
  font-size: 2.7vw;
  line-height: 93.4%;
  margin-right: 1vw;
}

input[type='text']::-webkit-input-placeholder {
  color: rgb(192, 181, 181);
}
input[type='text']::-moz-placeholder {
  color: rgb(192, 181, 181);
}

.todo-list-header {
  font-family: Aladin;
  font-style: normal;
  font-weight: normal;
  font-size: 4.6vw;
  line-height: 93.4%;
  text-align: center;
  color: rgba(0, 0, 0, 0.63);
  margin: 0;
}

.todo-list {
  height: 100%;
  width: 100%;
}

.todolist-header-block {
  padding-top: 3.2vw;
  height: 10%;
}

.create-todo-block {
  height: 12%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.todo-input {
  background: #fffbf1;
  border-radius: 87px;
  height: 3vw;
  width: 60%;
  font-family: Aladin;
  border: none;
  outline: none;
  padding-left: 1.5vw;
  font-size: 1.8vw;
  color: rgba(0, 0, 0, 0.63);
}

.add-todo-btn {
  background: #fb6b3e;
  border-radius: 87px;
  height: 3.2vw;
  width: 6vw;
  color: #ffffff;
  font-family: Aladin;
  font-size: 1.7vw;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-todo-btn:hover {
  background: #e26039;
}

.todos {
  padding-top: 1.3vw;
  height: 70%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
}

.todo {
  display: flex;
  height: 15%;
  width: 90%;
  background: #fffbf1;
  border: 1px solid rgba(0, 0, 0, 0.63);
  border-radius: 16px;
  margin-bottom: 1.8vw;
}

.todo-name {
  display: flex;
  width: 80%;
  align-items: center;
  padding-left: 2.2vw;
  font-size: 2.1vw;
  color: rgba(0, 0, 0, 0.63);
  font-family: Aladin;
  font-style: normal;
  font-weight: normal;
}

.delete-todo {
  width: 20%;
  height: 100%;
  background: #fffbf1;
  border: none;
  outline: none;
  border-radius: 0px 15px 15px 0px;
  border-left: 1px solid rgba(0, 0, 0, 0.63);
  cursor: pointer;
  transition: all 0.3s ease;
  padding: 0;
}

.delete-todo:hover {
  background-color: #fb6b3e;
}

.fa-trash-alt {
  color: #fb6b3e;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 1.8vw;
  border-radius: 0px 15px 15px 0px;
}

.fa-trash-alt:hover {
  color: #fffbf1;
}

.header {
  height: 20%;
}

.logo {
  height: 11vw;
  margin-left: 0.4vw;
  margin-top: 0.3vw;
}

html,
body {
  margin: 0;
}

.app {
  height: 100vh;
  display: flex;
  background-color: #e59379;
  font-family: 'Aladin', cursive;
}

.main-block {
  height: 100%;
  width: 68%;
}

.todolist-wrapper {
  height: 100%;
  width: 32%;
  background-color: #f3e1d5;
  box-shadow: -10px 4px 26px rgba(0, 0, 0, 0.25);
}

